import { MapBase } from "./map-base"
import { BLEND_MODES, IResourceDictionary, Sprite, Texture } from "pixi.js"

/**
 *
 */
export class MaensionOutside extends MapBase {

  constructor(
    resources: IResourceDictionary,
    width: number
  ) {
    super(resources, width, "maps.maension")

    let starryNight = new Sprite(
      this._textures!["sky.png"]
    );
    starryNight.scale.x = 2.56;
    starryNight.scale.y = 2.56;

    this._backgroundContainer.addChild(starryNight);

    let maension = new Sprite(
      this._textures!["Maension_WIP.png"]
    );

    maension.y = -40
    maension.scale.x = 2;
    maension.scale.y = 2;

    this._backgroundContainer.addChild(maension);

    const tree = this._textures!["tree.png"]
    const tree1 = new Sprite(tree);
    tree1.x = 100
    tree1.y = 350
    tree1.scale.x = 0.4
    tree1.scale.y = 0.4

    this._actorContainer.addChild(tree1)

    const tree_dead = this._textures!["tree_dead.png"]
    const tree2 = new Sprite(tree_dead);
    tree2.x = 50
    tree2.y = 180
    tree2.scale.x = 0.4
    tree2.scale.y = 0.4

    this._backgroundContainer.addChild(tree2)

    const light = new Sprite(
      Texture.WHITE
    );

    light.width = width
    light.height = this.height
    light.tint = 0x000000
    light.alpha = 0.8
    light.blendMode = BLEND_MODES.MULTIPLY
    light.zIndex = 1
    this._foregroundContainer.addChild(light)

    this._setup()
  }

  /**
   *
   * @private
   */
  protected _setup() {
    setInterval(() => {
      //z1.play();
    }, 30 * 1000)

    setInterval(() => {
      //z2.play();
    }, 67 * 1000)
  }
}