import { BLEND_MODES, filters, IResourceDictionary, Sprite } from "pixi.js"
import { SimpleLightmapFilter } from "@pixi/filter-simple-lightmap"
import { ObjectBase } from "./object-base"

/**
 *
 */
export class Flashlight extends ObjectBase {

  constructor(
    resources: IResourceDictionary
  ) {
    super(resources);

    const lightCone = new Sprite(
      resources!["flashlight"].texture
    );

    this.scale.set(2)
    lightCone.width = 100
    lightCone.height = 50
    lightCone.alpha = 0.2
    lightCone.tint = 0xefebd8
    lightCone.y = -20

    lightCone.zIndex = 99999
    const f = new SimpleLightmapFilter(resources!["flashlight"].texture)

    f.alpha = 0.1
    f.blendMode = BLEND_MODES.ADD
    lightCone.filters = [new filters.NoiseFilter(0.2), f]

    this.zIndex = 9999
    this.addChild(lightCone)
  }
}