import { ObjectBase } from "./object-base"
import { Text, IResourceDictionary, TextStyle } from "pixi.js"

export class Crosshair extends ObjectBase {

  constructor(
    resources: IResourceDictionary
  ) {
    super(resources);

    this.addChild(
      new Text("x", {
          fill: "#fff"
        } as TextStyle
      )
    )
  }
}
